* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
}


.App {
  /*padding-top: 3rem;*/
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(to bottom, lightslategrey, rgba(80, 140, 190, 0.1));
  flex-wrap: wrap;
}

@media screen and (max-width:768px) {
  .App {
    overflow-x: hidden;
  }
}
.Chatroom-container {
  width: 100%;
  min-height: 40rem;
  height: 100vh;
  background:linear-gradient(to bottom, rgba(80, 140, 190, 0.4), rgba(80, 140, 190, 0));
  padding:1rem;
  border-radius: 25px;
}

@media screen and (max-width: 1000px) {
  .Chatroom-container {
    width: 100%;
  }
}
.Chatroom-username-container {
  padding: 0 1rem 0 1rem;
  height: 100%;
  width: 25%;
  min-width: 14rem;
  font-family: 'Roboto', sans-serif;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-username {
  height: 100%;
  /*border: 1px solid rgba(30, 144, 255, 0.8);*/
  border-radius: 25px;
  background: linear-gradient(to bottom left,rgba(240,240,240, 0.8), white, rgba(240,240,240, 0.8));
}

.Chatroom-username-title {
  background-color: rgb(75,75,75);
  color: rgb(240,240,240);
  font-size: 130%;
  padding: 2rem 1rem 2rem 1rem;
  height: 5rem;
  border-bottom: 1px dashed rgba(50, 50, 50, 0.5);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  display: flex;
  align-items: center;
}

.Chatroom-username-list {
  min-height: 10rem;
}

.Chatroom-username-name {
  padding: 1rem;
  height: 3rem;
  border-bottom: 1px solid rgba(50, 50, 50, 0.3);
  display: flex;
  align-items: center;
  transition-duration: 100ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-username-icon {
  margin-right: 1rem;
  height: 3rem;
  width: 3rem;
  font-size: 150%;
  color: rgb(100,100,100);
  transition: 100ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-username-name:hover {
  background-color: rgba(50, 50, 50, 0.1);
  cursor: pointer;
}



.SendPictureContainer {
  padding-left: 0.5rem;
  height: 3rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.SendPicture-upload-button {
  background: none;
  border: none;
  font-size: 150%;
  color: rgb(150,150,150);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SendPicture-upload-button:hover {
  color:rgb(75,75,75);
}

.SendPicture-upload-button:focus {
  outline: none;
}

.SendPicture-button:hover {
  color:rgb(75,75,75);
  cursor: pointer;
}

.SendPicture-button:focus {
  outline: none;
}

.SendPicture-upload-text {
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  font-size: 70%;
  font-style: italic;
  color: rgb(150,150,150);
  
}

.alert-err {
  position: absolute;
  bottom:10%;
  right: 20%;
  height: 2.5rem;
  width: 25rem;
  background-color: rgba(220, 20, 60, 0.3);
  font-size: 90%;
  font-style: normal;
  color: ivory;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 400ms;
  transition-timing-function: ease-in-out;

}
.Chatroom-input-container {
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}
.Chatroom-form {
  width: 100%;
  background: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Chatroom-input {
  padding: 0.5rem 1rem 0.5rem 1rem;
  height: 2.5rem;
  width: 70%;
  font-size: 95%;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  border: 1px solid rgba(30, 144, 255, 0.8);
  border-right: none;
  
}

.Chatroom-input:focus {
  outline: none;
}

.Chatroom-send-btn {
  height: 2.5rem;
  width: 30%;
  padding-right: 0.5rem;
  color: rgba(30, 144, 255, 0.8);
  font-size: 110%;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  border: 1px solid rgba(30, 144, 255, 0.8);
  background: white;
  cursor: pointer;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-send-btn:focus {
  outline: none;
}

.Chatroom-send-btn:hover {
  background: dodgerblue;
  color: ivory;
}
.PictureTag {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.PicturePreview-img-typeA {
  width: auto;
  height: 25rem;
}

.PicturePreview-img-typeB {
  width: 25rem;
  height: auto;
  /*max-height: 70%;*/
}

.selection-container {
  position:absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.selection-container {
  position:absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.test {
  position: absolute;
  background: rgba(30, 144, 255, 0.5);
  border: 1px dotted dodgerblue;
}

.test-with-tag {
  background: none;
  border: 2px dotted dodgerblue;
}

.test-finished {
  opacity: 0.2;
  background: none;
}

.test-finished:hover {
  opacity: 1;
}

.tag-input {
  position: absolute;
  background: linear-gradient(to bottom left,rgba(240,240,240, 0.5), white, rgba(240,240,240, 0.5));
  text-align: center;
  width: 8rem;
  z-index: 3;
}

.tag-input:focus {
  outline: none;
}


.PicturePreview-buttons-container {
  position: absolute;
  bottom: 0.5rem;
  right: 0;
  width: 90%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  justify-content: space-between;
  
}

.PicturePreview-button {
  background: none;
  border: none;
  font-size: 150%;
  color: rgb(150,150,150);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.PicturePreview-accept:hover {
  color:dodgerblue;
  text-shadow: 0px 0px 2px dodgerblue;
}

.PicturePreview-delete:hover {
  color:crimson;
  text-shadow: 0px 0px 2px crimson;
}

.PicturePreview-send:hover {
  /*color:rgb(75,75,75);*/
  color:forestgreen;
  text-shadow: 0px 0px 10px chartreuse;
}

.PicturePreview-button:focus {
  outline:none;
}

.PicturePreview-send-button-text {
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  font-size: 70%;
  font-style: italic;
  color: rgb(150,150,150);
}
.PicturePreview-container {
  position: absolute;
  padding-top: 1rem;
  top: 0;
  left: 0;
  right: auto;
  height: 100%;
  width: 100%;
  background: rgba(240,240,240,0.8);
  border-radius:25px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.PicturePreview-title {
  font-weight: 100;
  margin-bottom: 0.5rem;
  font-size: 170%;
}


.Chatroom-message {
  position: relative;
  padding:1rem;
  height: 100%;
  width: 70%;
  min-width: 20rem;
  display: flex;
  border-radius: 25px;
  flex-direction: column;
  justify-content: space-between;
  background: linear-gradient(to bottom, teal, white);
}

.Userprofile-show-button{
  display: none;
  padding: 0;
  position: absolute;
  top:1rem;
  right: 1rem;
  border: 1px solid rgba(20,20,20,0.5);
  border-top-right-radius: 25px;
  border-bottom-left-radius: 25px;
  height: 2rem;
  width: 5rem;
  font-style: italic;
  cursor: pointer;
  transition-duration: 100ms;
  transition-timing-function:ease;
  z-index: 3;
}

.Userprofile-show-button:hover{
  font-weight: bold;
  text-shadow: 0px 0px 5px teal;
  font-size: 90%;
  height: 2.5rem;
  width: 5.5rem;
}

.Userprofile-show-button:focus {
  outline: none;
}

.Chatroom-chat {
  position: relative;
  padding: 1rem;
  padding-bottom: 2rem;
  height: 85%;
  width: 100%;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(to left, ghostwhite, AntiqueWhite);
  border-radius: 25px;
  border: 1px solid rgba(30, 144, 255, 0.8);
  
}

#scrollbars {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

.Chatroom-chat-name-message{
  padding: 0.3rem;
  min-width: 25%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 50%;
  min-width: 15rem;
  max-height: 20rem;
  overflow: hidden;
  margin-bottom: 0.5rem;
  background-color: white;
  border-radius: 15px;
  border: 1px solid rgba(50,50,50,0.1);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 85%;
  transition:200ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-chat-name-container {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  padding: 0.2rem;
  font-weight: bold;
}

.Chatroom-chat-message-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.2rem;
  color: rgba(20,20,20,0.8);
}

.Chatroom-chat-img-container-typeWider{
  height: auto;
  width: 100%;
  max-height: 20rem;
  cursor: pointer;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-chat-img-container-typeHigher{
  height: 15rem;
  width: auto;
  cursor: pointer;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}

.Chatroom-chat-img-container:hover {
  box-shadow: 0px 0px 10px 5px rgb(200,200,200);
} 

@media screen and (max-width:1024px){
  
  .Userprofile-show-button {
    display: block;
  }
}

@media screen and (max-width:768px) {
  .Chatroom-message {
    width: 100%;
  }
}

.UserProfile-container {
  padding: 0 1rem 0 1rem;
  height: 100%;
  width: 25%;
  min-width: 14rem;
  font-family: 'Roboto', sans-serif;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}

.UserProfile {
  
  height: 100%;
  /*border: 1px solid rgba(30, 144, 255, 0.8);*/
  border-radius: 25px;
  background: linear-gradient(to bottom right,rgba(240,240,240, 0.8), white, rgba(240,240,240, 0.8));
}

.UserProfile-username-container {
  height: 20rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.UserProfile-img-container {
  height: 12rem;
  width: 100%;
  margin-bottom: 1rem;
  font-size: 700%;
  background:rgb(75,75,75);
  color: rgb(240,240,240);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.UserProfile-edit {
  width: 80%;
  height: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.UserProfile-edit-button {
  background: none;
  border: none;
  color:rgb(150,150,150);
  font-size: 120%;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
}

.UserProfile-edit-button:hover {
  color: rgb(75,75,75);
}

.UserProfile-edit-button:focus {
  outline:none;
}

.UserProfile-editInputContainer {
  height: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.UserProfile-editInputContainer form {
  height: 100%;
  width:100%;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.UserProfile-edit-input {
  border: none;
  border-bottom: 1px solid rgb(150,150,150);
  background: none;
  width: 100%;
  height: 100%;
}

.UserProfile-edit-input:focus {
  outline: none;
  font-size: 100%;
  border-bottom: 1px solid dodgerblue;
}

.UserProfile-edit-input-button {
  background: none;
  border: none;
  color:darkgreen;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  font-size: 120%;
}

.UserProfile-edit-input-button:focus {
  outline:none;
}

.UserProfile-edit-input-cancel-button{
  background: none;
  border: none;
  color:crimson;
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  font-size: 120%;
}

.UserProfile-edit-input-cancel-button:focus {
  outline:none;
}

.UserProfile-show {
  transform: translateX(-250px);
  display: block;
}

.alert {
  padding:1rem;
  margin-top: 2rem;
  height: 4rem;
  width:100%;
  background-color: rgba(220, 20, 60, 0.3);
  font-size: 90%;
  font-style: normal;
  color: ivory;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width:1920px) {
  .UserProfile-show {
    transform: translateX(0px);
    display: block;
  }
}

@media screen and (max-width:1024px) {
  .UserProfile-container {
    transform: translateX(250px);
    display: none;
  }
  .UserProfile-show {
    transform: translateX(0px);
    display: block;
  }
}
.Chatroom {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}




.Home {
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: center;
}

.Home-form {
    margin-top: 3rem;
    padding-bottom: 2rem;
    position: relative;
    font-family: 'Raleway', sans-serif;
    font-size: 120%;
    font-style: italic;
    height: 20rem;
    width: 35rem;
    background: linear-gradient(to bottom right, white, rgba(240,240,240, 0.8), white);
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.Home-title-coontainer {
    padding: 0.5rem 0 0.5rem 0;
    height: 50%;
    width: 100%;
    background: rgb(75,75,75);
    color: white;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.alert-error {
    position: absolute;
    top:110%;
    height: 2.5rem;
    width: 25rem;
    background-color: rgba(220, 20, 60, 0.3);
    font-size: 90%;
    font-style: normal;
    color: ivory;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Home-input {
    margin-top: 1rem;
    height: 2.5rem;
    width: 15rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
    font-size: 105%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align: center;
    background: none;
    border: none;
    border-bottom: 1px solid rgba(20,20,20,0.4);
}

.Home-input:focus {
    outline: none;
    border-bottom: 1px solid rgba(30, 144, 255, 0.8);
}

.Home-submit-button {
    height: 3rem;
    width: 10rem;
    background: dodgerblue;
    color: whitesmoke;
    border: 1px solid dodgerblue;
    border-radius: 4px;
    font-size: 90%;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
}

.Home-submit-button:focus {
    outline: none;
}

.Home-submit-button:hover {
    text-shadow: 0px 0px 1px ivory;
    box-shadow: 0px 0px 5px 0px dodgerblue;

}
