.Chatroom-container {
  width: 100%;
  min-height: 40rem;
  height: 100vh;
  background:linear-gradient(to bottom, rgba(80, 140, 190, 0.4), rgba(80, 140, 190, 0));
  padding:1rem;
  border-radius: 25px;
}

@media screen and (max-width: 1000px) {
  .Chatroom-container {
    width: 100%;
  }
}